<template>
  <div class="mask" v-if="cartShow && JSON.stringify(cartItems) !== '{}'" @click="() => {handleCartShowChange(cartItems)}"></div>
    <div class="cart">
      <div class="cart__header" v-if="cartShow && JSON.stringify(cartItems) !== '{}'">
        <div class="cart__header__left">
          <div  v-if="allChecked" class="cart__header__left__checked-iconfont iconfont" @click="()=>{setAllChecked(shopId)}">&#xe618;</div>
          <div class="cart__header__left__unchecked-iconfont iconfont" @click="()=>{setAllChecked(shopId)}" v-else>&#xe619;</div>
          <div class="cart__header__left__text">全选</div>
        </div>
        <div class="cart__header__right">
          <div @click="() => {clearCart(shopId)}" class="cart__header__right__clear-car">清空购物车</div>
        </div>
      </div>
      <div class="cart___detail" v-if="cartShow && JSON.stringify(cartItems) !== '{}'">
        <div v-for="item of cartItems"
          :key="item.id"
          class="cart__detail__item">
          <!-- 购物车商品信息左侧，包括选中图标，商品图片，价格
          右侧为加减号，操作商品数量
          -->
          <div class="cart__detail__item__left">
            <!-- 勾号，选中图标 -->
            <div  @click="()=>{changeItemChecked(shopId,item.id)}" v-if="item.checked" class="cart__detail__item__left__checked-iconfont iconfont">&#xe618;</div>
            <div @click="()=>{changeItemChecked(shopId,item.id)}" v-else class="cart__detail__item__left__unchecked-iconfont">&#xe619;</div>
            <img :src="item.imgUrl" alt="" class="cart__detail__item__left__image">
            <!-- 商品信息 -->
            <div class="cart__detail__item__left__cart-info">
              <div class="cart__detail__item__left__cart-info__title">{{item.name}}</div>
              <div class="cart__detail__item__left__cart-info__price">
                  <div class="cart__detail__item__left__price__promotion">￥ {{item.promotionPrice}}</div>
                  <div class="cart__detail__item__left__price__original">￥ {{item.originalPrice}}</div>
              </div>
            </div>
          </div>
          <div class="cart__detail__item__right">
            <span @click="()=>{changeItemToCart(shopId,shopName,itemId,item,-1)}" v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']" class="cart__detail__item__right__minus-iconfont icon-minus iconfont"></span>
            <span v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']" class="cart__detail__item__right__number"> {{ cartData?.[shopId]?.itemList?.[item.id]?.['count'] }} </span>
            <span @click="()=>{changeItemToCart(shopId,shopName,itemId,item,1)}" class="cart__detail__item__right__plus-iconfont icon-plus-circle-fill iconfont"></span>
          </div>
        </div>
      </div>
      <div class="cart__count">
        <div class="cart__count__left" @click="() => {handleCartShowChange(cartItems)}">
          <div class="cart__count__left__icon">
            <img src="https://markdown-1253389072.cos.ap-nanjing.myqcloud.com/202202261637089.png" alt="" class="cart__count__left__icon__image">
                <span class="cart__count__left__icon__number">{{ total }} </span>
          </div>
          <div class="cart__count__left__count">
                总计：<span class="cart__count__left__count__price">{{sumPrice}}</span>
            </div>
        </div>
        <router-link :to = " total?{ path:`/order-conformation/${shopId}` }:''">
          <div class="cart__count__right">去结算</div>
        </router-link>
      </div>
    </div>
</template>
<script>
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { computed, ref } from '@vue/reactivity'
import { useCommonCartEffect } from '../../effects/commonCartEffect'
// 控制购物车显示的逻辑
const useCartShowEffect = () => {
  const cartShow = ref(false)
  const handleCartShowChange = () => {
    cartShow.value = !cartShow.value
  }
  return { cartShow, handleCartShowChange }
}
const useCartEffect = (cartShow) => {
  const route = useRoute()
  const store = useStore()
  const shopId = route.params.id
  const cartData = store.state.cartData
  const total = computed(() => {
    const itemList = cartData[shopId]?.itemList || {}
    let count = 0
    if (itemList) {
      for (const key in itemList) {
        count += itemList[key].count
      }
    }
    console.log(count)
    return count
  })
  const sumPrice = computed(() => {
    const itemList = cartData[shopId]?.itemList || {}
    let sum = 0.0
    console.log('itemList:')
    console.log(itemList)
    if (itemList) {
      for (const key in itemList) {
        if (itemList[key].checked) {
          // 只有选中的商品才计入总价
          console.log(`promotionPrice${itemList[key].promotionPrice}`)
          sum += itemList[key].count * itemList[key].promotionPrice
        }
      }
    }
    return sum.toFixed(2)
  })
  const cartItems = computed(() => {
    const cartItems = cartData[shopId]?.itemList || {}
    if (JSON.stringify(cartItems) === '{}') {
      cartShow.value = false
    }
    return cartItems
  })
  const allChecked = computed(() => {
    const itemList = cartData[shopId]?.itemList || {}
    let result = true
    for (const key in itemList) {
      if (!itemList[key].checked) {
        result = false
      }
    }
    return result
  })
  return { cartData, total, sumPrice, cartItems, shopId, allChecked }
}
export default {
  name: 'Cart',
  props: ['shopName'],
  setup () {
    const { cartData, total, sumPrice, cartItems, shopId, allChecked } = useCartEffect()
    const { cartShow, handleCartShowChange } = useCartShowEffect()
    const { changeItemToCart, changeItemChecked, clearCart, setAllChecked } = useCommonCartEffect()
    return { cartData, cartShow, handleCartShowChange, total, sumPrice, cartItems, shopId, changeItemToCart, changeItemChecked, clearCart, allChecked, setAllChecked }
  }
}
</script>
<style lang="scss" scoped>
.mask{
  position: fixed;
  background-color: rgba($color: #000000, $alpha: 0.5);
  top:0;
  right: 0;
  left: 0;
  bottom: 49rem;
}
.cart{
  position: fixed;
  background-color: white;
  bottom:0;
  left:0;
  right:0;
  width:375rem;
  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    height:52rem;
    padding: 0 18rem;
    width: 100%;
    border-bottom: 1px solid #f1f1f1;
    &__left{
      display: flex;
      align-items: center;
      &__checked-iconfont{
        font-size: 19rem;
        margin-right: 9rem;
        color:#0091ff;
      }
      &__unchecked-iconfont{
        font-size: 19rem;
        margin-right: 9rem;
        color:#0091ff;
      }
      &__text{
        font-family: PingFangSC-Regular;
        font-size: 14rem;
        line-height: 16rem;
        color:#333333;
      }
    }
    &__right{
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      line-height: 16rem;
      color:#333333;
      text-align: right;
      margin-right: 18rem;
    }
  }
  &__detail{
    padding: 0 18rem;
    box-sizing: border-box;
    width: 100%;
    &__item{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 18rem;
      &__left{
        display: flex;
        align-items: center;
        justify-content: center;
        &__checked-iconfont{
          color:#0091ff;
          margin-right: 16.4rem;
          font-size: 19rem;
        }
        &__unchecked-iconfont{
          color:#0091ff;
          margin-right: 16.4rem;
          font-size: 19rem;
        }
        &__image{
          width: 46rem;
          height: 46rem;
          margin-right: 16rem;
        }
        &__cart-info{
          display: flex;
          flex-direction: column;
          &__title{
            font-family: PingFangSC-Medium;
            font-size: 14rem;
            color:#333333;
            margin:16rem 0 6rem 0;
            line-height: 20rem;
          }
          &__price{
            display: flex;
            align-items: center;
            &__promotion{
              font-family: PingFangSC-Semibold;
              font-size: 14px;
              color: #E93B3B;
              line-height: 20px;
            }
            &__original{
              font-family: PingFangSC-Regular;
              font-size: 10px;
              color: #999999;
              line-height: 20px;
              text-decoration: line-through;
            }
          }
        }
      }
      &__right{
        display: flex;
        align-items: center;
        &__minus-iconfont{
          font-size: 17rem;
          color:#666666;
        }
        &__number{
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #333333;
          text-align: center;
          line-height: 16px;
        }
        &__plus-iconfont{
          font-size: 19rem;
          color:#0091ff;
        }
      }
    }
  }
  &__count{

    box-shadow: 0 -1px 1px 0 #f1f1f1;
    left: 0;
    right: 0;
    bottom: 0;
    height: 49rem;
    display: flex;
    justify-content: space-between;
    &__left{
      display: flex;
      align-items: center;
      &__icon{
        position: relative;
            margin-left: 24rem;
            margin-right: 32rem;
            &__image{
                width: 28rem;
                height: 26rem;
            }
            &__number{
              display: block;
              min-width: 20rem;
                height: 20rem;
                background: #e93b3b;
                border-radius: 10px;
                font-family: PingFangSC-Medium;
                font-size: 16px;
                transform: scale(0.5, 0.5);
                transform-origin: left top;
                color: #ffffff;
                text-align: center;
                position: absolute;
                left: 24rem;
                top: -4rem;
              }
            }
        &__count{
          display: flex;
          align-items: center;
          font-family: PingFangSC-Regular;
          font-size: 12rem;
          color:#333333;
          &__price{
            font-family: PingFangSC-Medium;
            font-size: 18rem;
            color:#e93b3b;
          }
        }
    }
    &__right{
        width: 98rem;
        height: 49rem;
        font-family: PingFangSC-Medium;
        background-color: #4fb0f9;
        font-size: 18rem;
        color:#ffffff;
