<template>
    <div class="content">
        <div class="content__left">
            <div @click="() => handleLeftItemChange(item.name)" v-for="item of leftItems" :key="item.name" tab-name="all" :class="{content__left__item:true,'content__left__item--active':item.name===currentItemName}">
              {{item.text}}
            </div>
        </div>
        <div class="content__right">
            <div class="content__right__item" v-for="item of rightItems" :key="item.id">
                <div class="content__right__item__left">
                    <img :src="item.imgUrl" alt="" class="content__right__item__left__image">
                </div>
                <div class="content__right__item__right">
                    <div class="content__right__item__right__top">{{item.name}}</div>
                    <div class="content__right__item__right__middle">月售{{item.sales}}件</div>
                    <div class="content__right__item__right__bottom">
                        <div class="content__right__item__right__bottom__price">
                            <div class="content__right__item__right__bottom__price__promotion">￥ {{item.promotionPrice}}</div>
                            <div class="content__right__item__right__bottom__price__original">￥ {{item.originalPrice}}</div>

                        </div>
                        <div class="content__right__item__right__bottom__count">
                            <span @click="()=>{changeItemToCart(shopId,shopName,item.id,item,-1)}" v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']" class="content__right__item__right__bottom__count__minus-iconfont icon-minus iconfont"></span>
                            <span v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']" class="content__right__item__right__bottom__count__number">  {{ cartData?.[shopId]?.itemList?.[item.id]?.['count'] }} </span>
                            <span @click="()=>{changeItemToCart(shopId,shopName,item.id,item,1)}" class="content__right__item__right__bottom__count__plus-iconfont icon-plus-circle-fill iconfont"></span>

                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>
<script>
import { ref, watchEffect } from 'vue'
import { useRoute } from 'vue-router'
import { get } from '../../utils/request'
import { useCommonCartEffect } from '../../effects/commonCartEffect'

const leftItems = [
  {
    name: 'all',
    text: '全部商品'
  },
  {
    name: 'seckill',
    text: '秒杀'
  },
  {
    name: 'fruit',
    text: '新鲜水果'
  },
  {
    name: 'snacks',
    text: '休闲食品'
  },
  {
    name: 'vegetable',
    text: '时令蔬菜'
  },
  {
    name: 'meat',
    text: '肉蛋家禽'
  }
]
const useTabEffect = () => {
  const currentItemName = ref('all')
  const handleLeftItemChange = (itemName) => {
    currentItemName.value = itemName
  }
  return { currentItemName, handleLeftItemChange }
}
const useContentEffect = (currentItemName) => {
  const rightItems = ref([])
  const route = useRoute()
  const shopId = route.params.id
  const getContentData = async (shopId, currentTab) => {
    const result = await get(`/api/shop/${shopId}/tab/${currentTab}`)
    console.log(result)
    rightItems.value = result.data.data
  }
  watchEffect(() => {
    getContentData(shopId, currentItemName.value)
  })
  return { rightItems, shopId }
}
export default {
  name: 'Content',
  props: ['shopName'],
  setup () {
    const { cartData, changeItemToCart } = useCommonCartEffect()
    const { currentItemName, handleLeftItemChange } = useTabEffect()
    const { rightItems, shopId } = useContentEffect(currentItemName)
    console.log('cartData:')
    console.log(cartData)
    return { shopId, cartData, leftItems, currentItemName, handleLeftItemChange, rightItems, changeItemToCart }
  }

}
</script>
<style lang="scss" scoped>
.content{
    position: absolute;
    top: 146rem;
    bottom: 49px;
    left: 0;
    right: 0;
    background-color: olivedrab;
    overflow-y: auto;
    display: flex;
    &__left{
        background-color: #f5f5f5;
        &__item{
            width: 76rem;
            height: 40rem;
            border-radius: 2rem;
            font-size: 14rem;
            font-family: PingFangSC-Regular;
            color: #333333;
            line-height: 40rem;
            text-align: center;
            &--active{
                background-color: #ffffff;
            }
        }
    }
    &__right{
        overflow: auto;
        background-color: #ffffff;
        flex-grow: 1;
        &__item{
            padding:0 12rem 12rem 16rem;
            display: flex;
            border-bottom: 1rem solid #f1f1f1;
            &__left{
                margin-right: 16rem;
                &__image{
                    width: 68rem;
                    height: 68rem;
                }
            }
            &__right{
                flex-grow: 1;
                &__top{
                    margin-bottom: 6rem;
                    line-height: 20rem;
                    font-family: PingFangSC-Medium;
                    font-size: 14rem;
                    color: #333333;
                }
                &__middle{
                    margin-bottom: 6rem;
                    font-family: PingFangSC-Regular;
                    font-size: 12rem;
                    color: #333333;
                    line-height: 16rem;
                }
                &__bottom{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    &__price{
                        display: flex;
                        align-items: center;
                        &__promotion{
                            font-family: PingFangSC-Semibold;
                            font-size: 14px;
                            color: #E93B3B;
                            line-height: 20px;
                            margin-right: 8rem;
                            &::first-letter{
                                font-size: 12rem;
                            }
                        }
                        &__original{
                            font-family: PingFangSC-Regular;
                            font-size: 10px;
                            color: #999999;
                            line-height: 20px;
                            &::first-letter{
                                font-size: 12rem;
                            }
                        }
                    }
                    &__count{
                        display: flex;
                        align-items: center;
                        &__minus-iconfont{
                            font-size: 20rem;
                            color: #666666;
                        }
                        &__number{
                            font-family: PingFangSC-Regular;
                            font-size: 14rem;
                            width: 20rem;
                            color: #333333;
                            line-height: 16rem;
                            margin-right: 10rem;
                            margin-left: 10rem;
                        }
                        &__plus-iconfont{
                            font-size: 17px;
                            color: #0091ff;
                        }
                    }
                }
            }
        }
    }
}
</style>
