<template>
  <div class="container">
    <div class="container__header">
      <div class="container__header__left">
        <span class="container__header__left-iconfont iconfont" @click="handleBackClick">&#xe6db;</span>
      </div>
      <div class="container__header__right">
        <span class="container__header__right-iconfont">&#xe6e1;</span>
        <input
          type="text"
          class="container__header__right-input"
          placeholder="输入商品名搜索商品"
        >
      </div>
    </div>
    <ShopInfo :item="item" :showButtom="false" v-if="item.imgUrl"></ShopInfo>
    <Content :shopName = "item.title"/>
    <Cart :shopName = "item.title"/>
  </div>
</template>
<script>
import { useRoute } from 'vue-router'
import ShopInfo from '../../components/ShopInfo.vue'
import Content from './Content'
import Cart from './Cart'
import { ref } from 'vue'
import { get } from '../../utils/request'
import { useBackRouterEffect } from '../../effects/backEffect.js'

const useShopInfoEffect = () => {
  const route = useRoute()
  const item = ref({})
  const getItemData = async () => {
    const result = await get(`/api/shop/${route.params.id}`)
    item.value = result.data.data
    console.log(`item.value:\n${item.value}`)
  }
  return { item, getItemData }
}
export default {
  name: 'Shop',
  components: { ShopInfo, Content, Cart },
  setup () {
    const { item, getItemData } = useShopInfoEffect()
    getItemData()
    const handleBackClick = useBackRouterEffect()
    // const handleBackClick = () => {
    //   console.log('hanjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjjj')
    // }
    return { item, handleBackClick }
  }
}
</script>
<style lang="scss" scoped>
@import '../../style/base.scss';
.container {
  padding: 0 18rem;
  &__header {
    display: flex;
    height: 32rem;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    margin: 16rem 0;
    &__left {
      color: #b6b6b6;
      font-size: 24rem;
      text-align: center;
      width: 12rem;
      height: 21rem;
      margin-right: 16rem;
    }
    &__right {
      flex: 1;
      position: relative;
      text-align: center;
      &-iconfont {
        position: absolute;
        left: 16rem;
        top: 8rem;
        font-size: 18rem;
        color: #b7b7b7;
      }
      &-input {
        width: 100%;
        box-sizing: border-box;
        background-color: #f5f5f5;
        border-radius: 16px;
        height: 32rem;
        padding: 0 44rem;
        font-size: 14rem;
        font-family: PingFangSC-Regular;
        color: #333333;
        line-height: 16px;
      }
    }
  }
}
</style>
